document.addEventListener('DOMContentLoaded', ()=> {

    const signup = document.querySelector('.header__auth_signup');
    const signin = document.querySelector('.header__auth_signin');
    const signupModal = document.querySelector('[data-signup]');
    const signinModal = document.querySelector('[data-signin]');
    const closeBtn = document.querySelectorAll('.modal__close_btn');
    const burgerMenu = document.querySelector('.mobile_menu');
    const burgerBtn = document.querySelector('.burger_btn');
    const lines = burgerBtn.querySelectorAll('span');

    signup.addEventListener('click', (e)=> {
        e.preventDefault();
        openSignupModal();
    });

    signin.addEventListener('click', (e)=> {
        e.preventDefault();
        openSigninModal();

    });

    closeBtn.forEach(i => {
        i.addEventListener('click', ()=> {
            closeModal();
        });
    });
    

    const openSignupModal = function() {
        signupModal.classList.remove('hide_modal');
        signupModal.classList.add('show_modal');
    };

    const openSigninModal = function() {
        signinModal.classList.remove('hide_modal');
        signinModal.classList.add('show_modal');
        
    };

    const closeModal = function() {
        if(signupModal.classList.contains('show_modal') || signinModal.classList.contains('show_modal')) {
            signupModal.classList.remove('show_modal'); 
            signupModal.classList.add('hide_modal');
            signinModal.classList.remove('show_modal');
            signinModal.classList.add('hide_modal');
              
        }
    };

    

    burgerBtn.addEventListener('click', ()=> {
        mobileMenu();
    }); 

    const mobileMenu = function() {
        burgerMenu.classList.toggle('show_burger');
        if(burgerMenu.classList.contains('show_burger')) {
            lines.forEach((i, num) => {
                if (num == 0) {
                    i.style.cssText = 'transform: rotate(45deg); top: calc(50% - 2px);';
                }
                if (num == 1) {
                    i.style.cssText = i.style.cssText = 'opacity: 0';
                }
                if (num == 2) {
                    i.style.cssText = i.style.cssText = 'transform: rotate(-45deg); bottom: calc(50% - 2px);';
                }
            });
        } else {
            lines.forEach((i, num) => {
                if(num == 0) {
                    i.style.cssText = 'transform: rotate(0); ';
                }
                if(num == 1) {
                    i.style.cssText = 'opacity: 1;';
                }
                if (num == 2) {
                    i.style.cssText = 'transform: rotate(0);';
                }
            });
        }
    };


});